import React,{useContext,useState,useEffect} from 'react';
import { MyContext } from '../../contexts/MyContext';
import { WEB_URL } from '../../contexts/Apilinks';
import Loader from 'react-loader-spinner';
import { useHistory } from "react-router-dom";
import axios from 'axios';
import {Link } from "react-router-dom"; 
import {toast} from "react-toastify";
import { FcAddImage } from "react-icons/fc";
import Navbarmenu from "../menu/Navbarmenu";
import Footer from "../Footer";
import '../../styles/_property.scss';

let BaseURL = WEB_URL;

function PostPropertyimg(props) {
  
  let history = useHistory();
  
  const [loadingadd, setloadingadd] = useState(false);
  const [userInfo, setuserInfo] = useState({
    file:[],
    filepreview:null,
    fileLiving:[],
    Livingpreview:null,
    fileBedrooms:[],
    Bedroomspreview:null,
    fileBathrooms:[],
    Bathroomspreview:null,
    fileKitchen:[],
    Kitchenpreview:null,
    fileFloorPlan:[],
    FloorPlanpreview:null,
    fileMasterPlan:[],
    MasterPlanpreview:null,
    fileLocation:[],
    Locationpreview:null,
    fileOthers:[],
    Otherspreview:null,
  });
  
  useEffect( () => {
    window.scrollTo(0, 0);
  }, []); 
         
        
  const onChangeValue = (e) => {
    setuserInfo({
      ...userInfo,
      [e.target.name]:e.target.value
    });
  } 
  const removeImg = (index) =>{
    var uploadimg = userInfo.file;
    var previewimg = userInfo.filepreview;
    uploadimg.splice(index, 1);
    previewimg.splice(index, 1);
    setuserInfo({
      ...userInfo,
      file:uploadimg,
      filepreview:previewimg,
  });
  }
  const removeImgLiving = (index) =>{
    var uploadimg = userInfo.fileLiving;
    var previewimg = userInfo.Livingpreview;
    uploadimg.splice(index, 1);
    previewimg.splice(index, 1);
    setuserInfo({...userInfo,fileLiving:uploadimg,Livingpreview:previewimg,});
  }
  const removeImgBedrooms = (index) =>{
    var uploadimg = userInfo.fileBedrooms;
    var previewimg = userInfo.Bedroomspreview;
    uploadimg.splice(index, 1);
    previewimg.splice(index, 1);
    setuserInfo({...userInfo,fileBedrooms:uploadimg,Bedroomspreview:previewimg,});
  }
  const removeImgBathrooms = (index) =>{
    var uploadimg = userInfo.fileBathrooms;
    var previewimg = userInfo.Bathroomspreview;
    uploadimg.splice(index, 1);
    previewimg.splice(index, 1);
    setuserInfo({...userInfo,fileBathrooms:uploadimg,Bathroomspreview:previewimg,});
  }
 
  const removeImgKitchen = (index) =>{
    var uploadimg = userInfo.fileKitchen;
    var previewimg = userInfo.Kitchenpreview;
    uploadimg.splice(index, 1);
    previewimg.splice(index, 1);
    setuserInfo({...userInfo,fileKitchen:uploadimg,Kitchenpreview:previewimg,});
  }
  const removeImgFloorPlan = (index) =>{
    var uploadimg = userInfo.fileFloorPlan;
    var previewimg = userInfo.FloorPlanpreview;
    uploadimg.splice(index, 1);
    previewimg.splice(index, 1);
    setuserInfo({...userInfo,fileFloorPlan:uploadimg,FloorPlanpreview:previewimg,});
  }
  const removeImgMasterPlan = (index) =>{
    var uploadimg = userInfo.fileMasterPlan;
    var previewimg = userInfo.MasterPlanpreview;
    uploadimg.splice(index, 1);
    previewimg.splice(index, 1);
    setuserInfo({...userInfo,fileMasterPlan:uploadimg,MasterPlanpreview:previewimg,});
  }
  const removeImgLocation = (index) =>{
    var uploadimg = userInfo.fileLocation;
    var previewimg = userInfo.Locationpreview;
    uploadimg.splice(index, 1);
    previewimg.splice(index, 1);
    setuserInfo({...userInfo,fileLocation:uploadimg,Locationpreview:previewimg,});
  }
  const removeImgOthers = (index) =>{
    var uploadimg = userInfo.fileOthers;
    var previewimg = userInfo.Otherspreview;
    uploadimg.splice(index, 1);
    previewimg.splice(index, 1);
    setuserInfo({...userInfo,fileOthers:uploadimg,Otherspreview:previewimg,});
  }
  const [fileempty, setfileempty] = useState(false);
  const [filetemp, setfiletemp] = useState(null);
  const [invalidImage, setinvalidImage] = useState(null);
     
var multipleimage = [];
  const handleChange = (event,phototype) => {
        if(event.target.files.length > 5){
          toast.error('Maximum images allowed : 5', {
            position: "top-center",autoClose: 5000,hideProgressBar: false, closeOnClick: true,pauseOnHover: true,draggable: true,
          }); 
          return;
        }
        for (const keyvalue of Object.keys(event.target.files)) {
          const imageFile = event.target.files[keyvalue];
          const imageFilname = event.target.files[keyvalue].name;
          if (!imageFile) {
            setinvalidImage('Please select image.');
            return false;
          }
          if (!imageFile.name.match(/\.(jpg|jpeg|png|gif|jfif|JPG|JPEG|PNG|GIF|JFIF)$/)) {
            setinvalidImage('Please select valid image JPG, PNG, GIF and JFIF files are allowed');
            return false;
          }
       

          let reader = new FileReader();
          reader.onload = (e) => {
            const img = new Image();
            img.onload = async() => {
          //setinvalidImage(' sdasdasf df sdf asdf ');
              //----------------------------- Resize img code -------------------------------------------------
              var canvas = document.createElement('canvas');
              var ctx = canvas.getContext("2d");
              ctx.drawImage(img, 0, 0);

              var MAX_WIDTH = 600;
              var MAX_HEIGHT = 600;
              var width = img.width;
              var height = img.height;

              if (width > height) {
                if (width > MAX_WIDTH) {
                  height *= MAX_WIDTH / width;
                  width = MAX_WIDTH;
                }
              } else {
                if (height > MAX_HEIGHT) {
                  width *= MAX_HEIGHT / height;
                  height = MAX_HEIGHT;
                }
              }
              canvas.width = width;
              canvas.height = height;
              var ctx = canvas.getContext("2d");
              ctx.drawImage(img, 0, 0, width, height);
              ctx.canvas.toBlob((blob) => {
                const file = new File([blob], event.target.files[keyvalue].name, {
                    type: phototype,
                    lastModified: Date.now(),
                    length:[keyvalue]
                });
                multipleimage.push(file);
              }, 'image/jpg', 1);
         
              //----------------------------------------------
              setfiletemp( URL.createObjectURL(event.target.files[keyvalue]))
              setfileempty(true);
             // setinvalidImage(null)
            };
            img.onerror = () => {setinvalidImage('Invalid image content.');return false;};
            //debugger
            img.src = e.target.result;
          };
          reader.readAsDataURL(event.target.files[keyvalue]);
        }
        setinvalidImage(null);
        console.log('qqqqqqqqqqqqqq',event.target.files);
        console.log('multipleimage',multipleimage);

        let imagesnew = [];
        for (let i = 0; i < event.target.files.length; i++) {
          imagesnew.push(URL.createObjectURL(event.target.files[i]))
        }
       
        if(phototype === 'Exterior' ){ 
           setuserInfo({...userInfo,file:multipleimage,filepreview:imagesnew,});
        }
        if(phototype === 'Living' ){ 
          setuserInfo({...userInfo,fileLiving:multipleimage,Livingpreview:imagesnew,});
        }
        if(phototype === 'Bedrooms' ){ 
          setuserInfo({...userInfo,fileBedrooms:multipleimage,Bedroomspreview:imagesnew,});
        }
        if(phototype === 'Bathrooms' ){ 
          setuserInfo({...userInfo,fileBathrooms:multipleimage,Bathroomspreview:imagesnew,});
        }
        if(phototype === 'Kitchen' ){ 
          setuserInfo({...userInfo,fileKitchen:multipleimage,Kitchenpreview:imagesnew,});
        }
        if(phototype === 'FloorPlan' ){ 
          setuserInfo({...userInfo,fileFloorPlan:multipleimage,FloorPlanpreview:imagesnew,});
        }
        if(phototype === 'MasterPlan' ){ 
          setuserInfo({...userInfo,fileMasterPlan:multipleimage,MasterPlanpreview:imagesnew,});
        }
        if(phototype === 'Location' ){ 
          setuserInfo({...userInfo,fileLocation:multipleimage,Locationpreview:imagesnew,});
        }
        if(phototype === 'Others' ){ 
          setuserInfo({...userInfo,fileOthers:multipleimage,Otherspreview:imagesnew,});
        }
        
      }
  const augalleryAddcategory = async (event) => {
    try {
        setloadingadd(true);
        event.preventDefault();
        event.persist();
        if(userInfo.file.length > 2 ){ 
          toast.error('Exterior View - Maximum images allowed : 2', {position: "top-center"}); setloadingadd(false);
          return;
        }
        if(userInfo.fileLiving.length > 2  ){ 
          toast.error('Living Room - Maximum images allowed : 2', {position: "top-center"}); setloadingadd(false);
          return;
        }
        if(userInfo.fileBedrooms.length > 2 ){ 
          toast.error('Bedrooms - Maximum images allowed : 2', {position: "top-center"}); setloadingadd(false);
          return;
        }
        if( userInfo.fileBathrooms.length > 2  ){ 
          toast.error('Bathrooms - Maximum images allowed : 2', {position: "top-center"}); setloadingadd(false);
          return;
        }
        if(userInfo.fileKitchen.length > 2  ){ 
          toast.error('Kitchen - Maximum images allowed : 2', {position: "top-center"}); setloadingadd(false);
          return;
        }
        if(userInfo.fileFloorPlan.length > 2  ){ 
          toast.error('Floor Plan - Maximum images allowed : 2', {position: "top-center"}); setloadingadd(false);
          return;
        }
        if(userInfo.fileMasterPlan.length > 2  ){ 
          toast.error('Master Plan - Maximum images allowed : 2', {position: "top-center"}); setloadingadd(false);
          return;
        }
        if(userInfo.fileLocation.length > 2  ){ 
          toast.error('Location Map - Maximum images allowed : 2', {position: "top-center"}); setloadingadd(false);
          return;
        }
        if(userInfo.fileOthers.length > 2){ 
          toast.error('Others - Maximum images allowed : 2', {position: "top-center"}); setloadingadd(false);
          return;
        }
       
        
        
      
        var formData = new FormData();
        console.log('uuuuuuuuuuu',userInfo.file);
        if(userInfo.file.length > 0  || userInfo.fileLiving.length > 0  || userInfo.fileBedrooms.length > 0  || userInfo.fileBathrooms.length > 0  || userInfo.fileKitchen.length > 0  || userInfo.fileFloorPlan.length > 0  || userInfo.fileMasterPlan.length > 0  || userInfo.fileLocation.length > 0  || userInfo.fileOthers.length > 0 ){ 
          if(userInfo.file.length > 0){ 
           for (const key of Object.keys(userInfo.file)) {
            formData.append('avatar', userInfo.file[key])
            formData.append('phototype', userInfo.file[key].type);
          }
        }
         if(userInfo.fileLiving.length > 0){ 
          for (const key of Object.keys(userInfo.fileLiving)) {
            formData.append('avatar', userInfo.fileLiving[key])
            formData.append('phototype', userInfo.fileLiving[key].type);
          }
         }
         if(userInfo.fileBedrooms.length > 0 ){ 
          for (const key of Object.keys(userInfo.fileBedrooms)) {
            formData.append('avatar', userInfo.fileBedrooms[key])
            formData.append('phototype', userInfo.fileBedrooms[key].type);
          }
         }
         if(userInfo.fileBathrooms.length > 0 ){
          for (const key of Object.keys(userInfo.fileBathrooms)) {
            formData.append('avatar', userInfo.fileBathrooms[key])
            formData.append('phototype', userInfo.fileBathrooms[key].type);
          } 
         }
         if(userInfo.fileKitchen.length > 0 ){ 
          for (const key of Object.keys(userInfo.fileKitchen)) {
            formData.append('avatar', userInfo.fileKitchen[key])
            formData.append('phototype', userInfo.fileKitchen[key].type);
          }
         }
         if(userInfo.fileFloorPlan.length > 0 ){ 
          for (const key of Object.keys(userInfo.fileFloorPlan)) {
            formData.append('avatar', userInfo.fileFloorPlan[key])
            formData.append('phototype', userInfo.fileFloorPlan[key].type);
          }
         }
         if(userInfo.fileMasterPlan.length > 0 ){
          for (const key of Object.keys(userInfo.fileMasterPlan)) {
            formData.append('avatar', userInfo.fileMasterPlan[key])
            formData.append('phototype', userInfo.fileMasterPlan[key].type);
          } 
         }
         if(userInfo.fileLocation.length > 0 ){ 
          for (const key of Object.keys(userInfo.fileLocation)) {
            formData.append('avatar', userInfo.fileLocation[key])
            formData.append('phototype', userInfo.fileLocation[key].type);
          }
        }
         if(userInfo.fileOthers.length > 0 ){ 
          for (const key of Object.keys(userInfo.fileOthers)) {
            formData.append('avatar', userInfo.fileOthers[key])
            formData.append('phototype', userInfo.fileOthers[key].type);
          }
         }
          
        }else{
          formData.append('avatar', '');
        }
                    
        formData.append('ids',props.match.params.AddPreview);
        axios.post(BaseURL+`addimgpropertyall`, formData).then(res => {
          console.log(res)
          setloadingadd(false);
          if(res.error_code === 1){
            toast.error('Server Problem in uploading image files.Please try again', {position: "top-center",autoClose: 5000,hideProgressBar: false, closeOnClick: true,pauseOnHover: true,draggable: true,}); 
            setloadingadd(false);
            return;
         }
          if(res.data.success === 1){
            toast( ' Post Author Updates  Successfully.', {position: "top-right",autoClose: 5000,hideProgressBar: false, closeOnClick: true,pauseOnHover: true,draggable: true,});  
              setloadingadd(false);
              event.target.reset(); 

         }else{
          setloadingadd(false);
          toast.error( 'Server Problem in uploading image files. Please Try Again', {position: "top-right",autoClose: 5000,hideProgressBar: false, closeOnClick: true,pauseOnHover: true,draggable: true,});  
        }
        setloadingadd(false);
      })
       } catch (error) { throw error;}    
}
   

const [isExterior, setExterior] = useState(true);
const [isLiving, setLiving] = useState(false);
const [isBedrooms, setBedrooms] = useState(false);
const [isBathrooms, setBathrooms] = useState(false);
const [isKitchen, setKitchen] = useState(false);
const [isFloorPlan, setFloorPlan] = useState(false);
const [isMasterPlan, setMasterPlan] = useState(false);
const [isLocation, setLocation] = useState(false);
const [isOthers, setOthers] = useState(false);
const Exterior = () =>{
  setExterior(true);setLiving(false);setBedrooms(false);setBathrooms(false);setKitchen(false);setFloorPlan(false);setMasterPlan(false);setLocation(false);setOthers(false);
  setinvalidImage(null);
}
const Living = () =>{
  setExterior(false);setLiving(true);setBedrooms(false);setBathrooms(false);setKitchen(false);setFloorPlan(false);setMasterPlan(false);setLocation(false);setOthers(false);
  setinvalidImage(null);
}
const Bedrooms = () =>{
  setExterior(false);setLiving(false);setBedrooms(true);setBathrooms(false);setKitchen(false);setFloorPlan(false);setMasterPlan(false);setLocation(false);setOthers(false);
  setinvalidImage(null);
}
const Bathrooms = () =>{
  setExterior(false);setLiving(false);setBedrooms(false);setBathrooms(true);setKitchen(false);setFloorPlan(false);setMasterPlan(false);setLocation(false);setOthers(false);
  setinvalidImage(null);
}
const Kitchen = () =>{
  setExterior(false);setLiving(false);setBedrooms(false);setBathrooms(false);setKitchen(true);setFloorPlan(false);setMasterPlan(false);setLocation(false);setOthers(false);
  setinvalidImage(null);
}
const FloorPlan = () =>{
  setExterior(false);setLiving(false);setBedrooms(false);setBathrooms(false);setKitchen(false);setFloorPlan(true);setMasterPlan(false);setLocation(false);setOthers(false);
  setinvalidImage(null);
}
const MasterPlan = () =>{
  setExterior(false);setLiving(false);setBedrooms(false);setBathrooms(false);setKitchen(false);setFloorPlan(false);setMasterPlan(true);setLocation(false);setOthers(false);
  setinvalidImage(null);
}
const Location = () =>{
  setExterior(false);setLiving(false);setBedrooms(false);setBathrooms(false);setKitchen(false);setFloorPlan(false);setMasterPlan(false);setLocation(true);setOthers(false);
  setinvalidImage(null);
}
const Others = () =>{
  setExterior(false);setLiving(false);setBedrooms(false);setBathrooms(false);setKitchen(false);setFloorPlan(false);setMasterPlan(false);setLocation(false);setOthers(true);
  setinvalidImage(null);
}

        return(<>
         <Navbarmenu />
<div className="post__property">
  <div className="container">
		<div className="row">
      <div className="col-md-12" style={{padding:0}}>
        <form onSubmit={augalleryAddcategory} className="update__forms">
       
          <div className="clearfix"></div>
          <h3 className="myaccount-content"> Photos </h3>
          <h6 className="myaccount-content"> It's Optional! But, don't forget to upload them later. </h6>
          <div className="clearfix"></div>
          <ul>
            <li onClick={Exterior}>Exterior View</li>
            <li onClick={Living}>Living Room</li>
            <li onClick={Bedrooms}>Bedrooms</li>
            <li onClick={Bathrooms}>Bathrooms</li>
            <li onClick={Kitchen}>Kitchen</li>
            <li onClick={FloorPlan}>Floor Plan</li>
            <li onClick={MasterPlan}>Master Plan</li>
            <li onClick={Location}>Location Map</li>
            <li onClick={Others}>Others</li>
          </ul>
          <div className="clearfix"></div>
          <div className="form-row">
          {isExterior === true ? <>
            <div className="form-group col-md-12">
              <label className="font-weight-bold"> Images  </label>
              <div className="clearfix"></div>
              <div className="dropzone-wrapper">
                <div className="dropzone-desc">
                  <FcAddImage />
                  <p>Choose an image file or drag it here.   <span className="adverts-form-required2"> <b> Maximum: five images Only JPG, PNG,  GIF and JFIF files are allowed </b> </span> </p>
                </div>
                <input type="file" name="file" className="form-control dropzone" placeholder="Title"  onChange={(event)=>handleChange(event,'Exterior')}  multiple />
                {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null } 
              </div>
            </div>
            {userInfo.filepreview && (<>
              {userInfo.filepreview.map((img, index) => (  <div key={index} >
                <img className="previewimg" src={img} alt={"image-" + index}  />;
                <p onClick={()=>removeImg(index)}> Remove </p>
              
                </div>
              ))}
            </>)}
             {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null }   
          </>: null}
{/* Living Room ---------------------------------------------------- */}
          {isLiving === true ? <>
             <div className="form-group col-md-12">
              <label className="font-weight-bold"> Living Room </label>
              <div className="clearfix"></div>
              <div className="dropzone-wrapper">
                <div className="dropzone-desc">
                  <FcAddImage />
                  <p>Choose an image file or drag it here.   <span className="adverts-form-required2"> <b> Maximum: five images Only JPG, PNG,  GIF and JFIF files are allowed </b> </span> </p>
                </div>
                <input type="file" name="fileLiving" className="form-control dropzone" placeholder="Title"  onChange={(event)=>handleChange(event,'Living')}  multiple />
                {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null } 
              </div>
            </div>
            {userInfo.Livingpreview && (<>
              {userInfo.Livingpreview.map((img, index) => (  <div key={index} >
                <img className="previewimg" src={img} alt={"image-" + index}  />;
                <p onClick={()=>removeImgLiving(index)}> Remove </p>
              
                </div>
              ))}
            </>)}
             {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null }   
          </>: null}

{/* Bedrooms ---------------------------------------------------- */}
          {isBedrooms === true ? <>
             <div className="form-group col-md-12">
              <label className="font-weight-bold"> Bedrooms </label>
              <div className="clearfix"></div>
              <div className="dropzone-wrapper">
                <div className="dropzone-desc">
                  <FcAddImage />
                  <p>Choose an image file or drag it here.   <span className="adverts-form-required2"> <b> Maximum: five images Only JPG, PNG,  GIF and JFIF files are allowed </b> </span> </p>
                </div>
                <input type="file" name="fileBedrooms" className="form-control dropzone" placeholder="Title"  onChange={(event)=>handleChange(event,'Bedrooms')}  multiple />
                {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null } 
              </div>
            </div>
            {userInfo.Bedroomspreview && (<>
              {userInfo.Bedroomspreview.map((img, index) => (  <div key={index} >
                <img className="previewimg" src={img} alt={"image-" + index}  />;
                <p onClick={()=>removeImgBedrooms(index)}> Remove </p>
              
                </div>
              ))}
            </>)}
             {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null }   
          </>: null}
          
{/* Bathrooms ---------------------------------------------------- */}
{isBathrooms === true ? <>
             <div className="form-group col-md-12">
              <label className="font-weight-bold"> Bathrooms </label>
              <div className="clearfix"></div>
              <div className="dropzone-wrapper">
                <div className="dropzone-desc">
                  <FcAddImage />
                  <p>Choose an image file or drag it here.   <span className="adverts-form-required2"> <b> Maximum: five images Only JPG, PNG,  GIF and JFIF files are allowed </b> </span> </p>
                </div>
                <input type="file" name="fileBathrooms" className="form-control dropzone" placeholder="Title"  onChange={(event)=>handleChange(event,'Bathrooms')}  multiple />
                {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null } 
              </div>
            </div>
            {userInfo.Bathroomspreview && (<>
              {userInfo.Bathroomspreview.map((img, index) => (  <div key={index} >
                <img className="previewimg" src={img} alt={"image-" + index}  />;
                <p onClick={()=>removeImgBathrooms(index)}> Remove </p>
              
                </div>
              ))}
            </>)}
             {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null }   
          </>: null}
         
{/* Kitchen ---------------------------------------------------- */}
{isKitchen === true ? <>
             <div className="form-group col-md-12">
              <label className="font-weight-bold"> Kitchen </label>
              <div className="clearfix"></div>
              <div className="dropzone-wrapper">
                <div className="dropzone-desc">
                  <FcAddImage />
                  <p>Choose an image file or drag it here.   <span className="adverts-form-required2"> <b> Maximum: five images Only JPG, PNG,  GIF and JFIF files are allowed </b> </span> </p>
                </div>
                <input type="file" name="fileKitchen" className="form-control dropzone" placeholder="Title"  onChange={(event)=>handleChange(event,'Kitchen')}  multiple />
                {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null } 
              </div>
            </div>
            {userInfo.Kitchenpreview && (<>
              {userInfo.Kitchenpreview.map((img, index) => (  <div key={index} >
                <img className="previewimg" src={img} alt={"image-" + index}  />;
                <p onClick={()=>removeImgKitchen(index)}> Remove </p>
              
                </div>
              ))}
            </>)}
             {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null }   
          </>: null}
         
{/* Floor Plan ---------------------------------------------------- */}
{isFloorPlan === true ? <>
             <div className="form-group col-md-12">
              <label className="font-weight-bold"> Floor Plan </label>
              <div className="clearfix"></div>
              <div className="dropzone-wrapper">
                <div className="dropzone-desc">
                  <FcAddImage />
                  <p>Choose an image file or drag it here.   <span className="adverts-form-required2"> <b> Maximum: five images Only JPG, PNG,  GIF and JFIF files are allowed </b> </span> </p>
                </div>
                <input type="file" name="fileFloorPlan" className="form-control dropzone" placeholder="Title"  onChange={(event)=>handleChange(event,'FloorPlan')}  multiple />
                {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null } 
              </div>
            </div>
            {userInfo.FloorPlanpreview && (<>
              {userInfo.FloorPlanpreview.map((img, index) => (  <div key={index} >
                <img className="previewimg" src={img} alt={"image-" + index}  />;
                <p onClick={()=>removeImgFloorPlan(index)}> Remove </p>
              
                </div>
              ))}
            </>)}
             {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null }   
          </>: null}
         
{/* Master Plan ---------------------------------------------------- */}
{isMasterPlan === true ? <>
             <div className="form-group col-md-12">
              <label className="font-weight-bold"> Master Plan </label>
              <div className="clearfix"></div>
              <div className="dropzone-wrapper">
                <div className="dropzone-desc">
                  <FcAddImage />
                  <p>Choose an image file or drag it here.   <span className="adverts-form-required2"> <b> Maximum: five images Only JPG, PNG,  GIF and JFIF files are allowed </b> </span> </p>
                </div>
                <input type="file" name="fileMasterPlan" className="form-control dropzone" placeholder="Title"  onChange={(event)=>handleChange(event,'MasterPlan')}  multiple />
                {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null } 
              </div>
            </div>
            {userInfo.MasterPlanpreview && (<>
              {userInfo.MasterPlanpreview.map((img, index) => (  <div key={index} >
                <img className="previewimg" src={img} alt={"image-" + index}  />;
                <p onClick={()=>removeImgMasterPlan(index)}> Remove </p>
              
                </div>
              ))}
            </>)}
             {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null }   
          </>: null}
         
{/* Location Map ---------------------------------------------------- */}
{isLocation === true ? <>
             <div className="form-group col-md-12">
              <label className="font-weight-bold"> Location Map </label>
              <div className="clearfix"></div>
              <div className="dropzone-wrapper">
                <div className="dropzone-desc">
                  <FcAddImage />
                  <p>Choose an image file or drag it here.   <span className="adverts-form-required2"> <b> Maximum: five images Only JPG, PNG,  GIF and JFIF files are allowed </b> </span> </p>
                </div>
                <input type="file" name="fileLocation" className="form-control dropzone" placeholder="Title"  onChange={(event)=>handleChange(event,'Location')}  multiple />
                {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null } 
              </div>
            </div>
            {userInfo.Locationpreview && (<>
              {userInfo.Locationpreview.map((img, index) => (  <div key={index} >
                <img className="previewimg" src={img} alt={"image-" + index}  />;
                <p onClick={()=>removeImgLocation(index)}> Remove </p>
              
                </div>
              ))}
            </>)}
             {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null }   
          </>: null}
         
         
{/* Others ---------------------------------------------------- */}
{isOthers === true ? <>
             <div className="form-group col-md-12">
              <label className="font-weight-bold"> Others </label>
              <div className="clearfix"></div>
              <div className="dropzone-wrapper">
                <div className="dropzone-desc">
                  <FcAddImage />
                  <p>Choose an image file or drag it here.   <span className="adverts-form-required2"> <b> Maximum: five images Only JPG, PNG,  GIF and JFIF files are allowed </b> </span> </p>
                </div>
                <input type="file" name="fileOthers" className="form-control dropzone" placeholder="Title"  onChange={(event)=>handleChange(event,'Others')}  multiple />
                {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null } 
              </div>
            </div>
            {userInfo.Otherspreview && (<>
              {userInfo.Otherspreview.map((img, index) => (  <div key={index} >
                <img className="previewimg" src={img} alt={"image-" + index}  />;
                <p onClick={()=>removeImgOthers(index)}> Remove </p>
              
                </div>
              ))}
            </>)}
             {invalidImage !== null ? <p className="error" style={{float:"left",textAlign: "left"}}>{invalidImage}</p> : null }   
          </>: null}



















                {invalidImage !== null ? null :<>  
                  {loadingadd ?
                            <>
                                   <Loader type="ThreeDots" color="#7b2e00" height={67} width={67} style={{float: "right",width: "100%",textAlign: "right"}}/>
                            </>
                         : 
                     <div className="form-group col-sm-12 text-right">
             
                            <button type="submit" className="btn btn-primary"> Submit  </button>
                            
                     </div> 
                   }
             
              </>} 
              </div>
              
          </form>
          </div>


  
          
          </div>
          </div>





          </div>
          <Footer />         
			  </>
        );
    }

export default PostPropertyimg;